// @flow
import React from 'react';
import { Card, CardBody, Row, Col } from 'reactstrap';

const Statistics = () => {
    return (
        <React.Fragment>
            <Row>
                <Col>
                    <Card className="widget-inline">
                        <CardBody className="p-0">
                            <Row className="no-gutters">
                                <Col sm={6} xl={3}>
                                    <Card className="shadow-none m-0">
                                        <CardBody className="text-center">
                                            <i className="mdi mdi-alpha-p-box"></i>
                                            <h3>
                                                <span>29P</span>
                                            </h3>
                                            <p className="text-muted font-15 mb-0">Total Point</p>
                                        </CardBody>
                                    </Card>
                                </Col>

                                <Col sm={6} xl={3}>
                                    <Card className="card shadow-none m-0 border-left">
                                        <CardBody className="text-center">
                                            <i className="uil-bolt-alt"></i>
                                            <h3>
                                                <span>20kWh</span>
                                            </h3>
                                            <p className="text-muted font-15 mb-0">  </p>
                                        </CardBody>
                                    </Card>
                                </Col>

                                <Col sm={6} xl={3}>
                                    <Card className="card shadow-none m-0 border-left">
                                        <CardBody className="text-center">
                                            <i className="mdi mdi-engine-off-outline"></i>
                                            <h3>
                                                <span>31</span>
                                            </h3>
                                            <p className="text-muted font-15 mb-0">  </p>
                                        </CardBody>
                                    </Card>
                                </Col>

                                <Col sm={6} xl={3}>
                                    <Card className="card shadow-none m-0 border-left">
                                        <CardBody className="text-center">
                                            <i className="uil-trees"></i>
                                            <h3>
                                                <span>10</span> <i className="mdi mdi-arrow-up text-success"></i>
                                            </h3>
                                            <p className="text-muted font-15 mb-0">   </p>
                                        </CardBody>
                                    </Card>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Col>
            </Row>
        </React.Fragment>
    );
};

export default Statistics;
